@import url("https://fonts.googleapis.com/css?family=Open+Sans:400");

body,
html {
  height: 100%;
  margin: 0;
}

body {
  font-family: "Open Sans";
}

#root {
  height: 100%;
}

.container {
  background-image: linear-gradient(
    to right bottom,
    #8af5ff,
    #69c9d3,
    #489fa8,
    #287780,
    #005159
  );
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

a,
a:visited,
a:hover,
a:active {
  color: inherit;
}

.name {
  color: #ffffff;
  font-size: 10vw;
  font-weight: bold;
  text-align: center;
}

.social {
  display: flex;
  width: 80%;
  flex-direction: row;
  color: #ffffff;
  font-size: 8vw;
  text-align: center;
  margin: 0;
  margin-top: 20%;
  justify-content: space-around;
}

.socialIcons {
  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
}
.socialIcons:hover {
  text-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}

/* Desktops and laptops ----------- */
@media only screen and (min-width: 1224px) {
  .social {
    font-size: 4vw;
  }
}
